import React, { useState, useEffect } from "react";
import "./header.css";

const Header = ({ toggleMode, darkMode }) => {
  // Fixed Header Effect
  useEffect(() => {
    const handleScroll = () => {
      const header = document.querySelector(".header");
      if (header) {
        header.classList.toggle("active", window.scrollY > 100);
      }
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  // Toggle Menu State
  const [Mobile, setMobile] = useState(false);

  const handleToggleMenu = (e) => {
    e.stopPropagation(); // Stop event propagation
    setMobile((prev) => !prev); // Ensure state updates properly
  };

  return (
    <>
      <header className="header">
        <div className="container d_flex">
          <h1 className="sub-title">
            <span> NADIM </span>
            SHAHRIAR
          </h1>

          <div className="navlink">
            <ul
              className={Mobile ? "nav-links-mobile" : "link f_flex uppercase"}
              onClick={(e) => e.stopPropagation()} // Prevent closing on first click
            >
              <li>
                <a href="#home">home</a>
              </li>
              <li>
                <a href="#features">features</a>
              </li>
              <li>
                <a href="#portfolio">portfolio</a>
              </li>
              <li>
                <a href="#resume">resume</a>
              </li>
              <li>
                <a href="#skill">skill</a>
              </li>
              <li>
                <a href="#blog">Achievements</a>
              </li>
              <li>
                <a href="#contact">contact</a>
              </li>
              <li>
                <button onClick={toggleMode} className="home-btn">
                  { darkMode? "Light Mode" : "Dark Mode" }
                </button>
              </li>
            </ul>

            <button className="toggle" onClick={handleToggleMenu}>
              {Mobile ? (
                <i className="fas fa-times close home-btn"></i>
              ) : (
                <i className="fas fa-bars open"></i>
              )}
            </button>
          </div>
        </div>
      </header>
    </>
  );
};

export default Header;
